<template>
 <button class="up-button" @click="scrollToTop()" ref="up-button">
   <img src="../assets/up-arrow.svg" alt="Back to top button">
 </button>
</template>

<script>
// import i18n from '@/i18n'

export default {
  name: 'UpButton',
  mounted: function() {
    let options = {
      rootMargin: '0px',
      threshold: 0.8
    }

    let target = this.$parent.$refs['arpilit'].$el;

    let that = this;
    let observer = new IntersectionObserver(function(entries, observer) {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          let button = that.$refs['up-button'];
          button.style['visibility'] = 'visible';
          button.style['opacity'] = '0.9';
        }
      })
    }, options);
    observer.observe(target);
  },
  methods: {
    scrollToTop: function() {
      let el = this.$parent.$refs['myth'].$el;
      el.scrollIntoView({behavior: "smooth"});
      setTimeout(() => {
          let button = this.$refs['up-button'];
          button.style['opacity'] = '0';
          button.style['visibility'] = 'hidden';
      }, 500);
    }
  }
}
</script>

<style lang="scss">
  .up-button {
    visibility: hidden;
    opacity: 0;
    z-index: 3;
    position: fixed;
    background: none transparent;
    border: medium none;
    color: white;
    font-size: 16px;
    height: 1.6em;
    padding: 0;
    bottom: 2em;
    left: 5em;
    mix-blend-mode: difference;
    cursor: pointer;
    transition: 200ms ease-in-out;
    img {
      height: 100%;
      width: auto;
    }
    &:hover {
      font-weight: bold;
    }
  }
</style>
