<template>
 <button class="locale-button" @click="switchLocale()">
   EN | עב
 </button>
</template>

<script>
// import i18n from '@/i18n'

export default {
  name: 'LocaleButton',
  methods: {
    switchLocale: function() {
      if (this.isOnHebrew()) {
        i18n.locale = "en";
      }
      else {
        i18n.locale = "he";
      }
    },
    isOnHebrew() {
      return i18n.locale === "he";
    }
  }
}
</script>

<style lang="scss">
  .locale-button {
    z-index: 1;
    position: absolute;
    background: none transparent;
    border: medium none;
    color: white;
    font-size: 16px;
    padding: 0;
    top: 2em;
    left: 3em;
    cursor: pointer;
    transition: 200ms ease-in-out;
    &:hover {
      font-weight: bold;
    }
  }
</style>
